@import "css.gg/icons/scss/search";

$primary-color: #2823fb;
$dropdown-shadow-color: rgba(0, 0, 0, 0.15);
$item-on-hover-bg-color: rgb(243, 247, 255);
$item-color: rgb(152, 164, 197);

.select {
	width: auto;
	display: block;
	margin: 0.1rem;
	padding: 0.1rem;
	position: relative;
	.label {
		display: block;
		font-size: 1rem;
		margin-bottom: 0.5rem;
	}
	.inputWrapper {
		display: flex;
		flex-direction: row;
		justify-content: flex-start;
		align-items: center;
		border: 1px solid $primary-color;
		border-radius: 0.25rem;
		padding: 1rem;
		.icon {
			@extend .gg-search;
			--ggs: 0.5rem;
			color: $primary-color;
		}
		.input {
			flex-basis: auto;
			font-size: large;
			border: none;
			outline: none;
			padding-left: 1rem;
			margin: 0px;
			width: 100%;
			flex-grow: 1;
		}
		&:hover {
			background-color: white;
			border: 1px solid $primary-color;
		}
		&:focus {
			background-color: white;
			border: 1px solid $primary-color;
		}
	}

	.dropdown {
		position: absolute;
		box-sizing: content-box;
		width: 100%;
		z-index: 1;
		margin: 1rem 0rem 0rem 0rem;
		padding: 0.0rem;
		text-align: left;
		box-shadow: 0 0.5rem 1rem $dropdown-shadow-color;
		border-radius: 0.25rem;
		.item {
			box-sizing: content-box;
			margin: 0rem 0.5rem 0rem 0.5rem;
			padding: 1rem;
			cursor: pointer;
			border-radius: 0.25rem;
			&:first-child {
				margin-top: 0.5rem;
			}
			&:last-child {
				margin-bottom: 0.5rem;
			}
			&:active {
				background-color: $item-on-hover-bg-color;
			}
			&:hover {
				background-color: $item-on-hover-bg-color;
			}
			.title {
				display: block;
				width: 100%;
				font-size: 1rem;
				background-color: transparent;
				margin: 0px;
				padding: 0px;
			}
			.subtitle {
				display: block;
				width: 100%;
				font-size: 0.8rem;
				color: $item-color;
				background-color: transparent;
				margin: 0px;
				margin-top: 0.25rem;
				padding: 0px;
			}
		}
	}
}
